body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.wrapper{
  width: 100%;
  display: flex;
  justify-content: center;
}
.header-wrapper{
  background-color: #e8e8e8;
}
.header {
  width: 1000px;
  height: 125px;
  background-color: #e8e8e8;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: Georgia;
}

.logo {
  width: 315px;
  height: 29px;

  display: inline-flex;
  margin-left: 20px;
}

.logo-text {
  font-size: 26px;
  text-transform: uppercase;
  color: #333333;
}
.logo-text::first-letter {
  font-size: 36px;
  line-height: 23px;
}

.logo-separator {
  width: 1px;
  height: 25px;
  background-color: #999999;
  margin-left: 9px;
  margin-right: 7px;
}

.logo-describe {
  width: 102px;
  height: 28px;
  color: #999999;
  font-size: 12px;
  line-height: normal;
}

.header-menu {
  width: 558px;
}

.header-menu-list {
  display: flex;
  list-style-type: none;
  text-align: center;
}

.header-menu-item {
  flex-grow: 1;
}

.menu-text {
  font-size: 12px;
  text-transform: uppercase;
  vertical-align: middle;
  color: #666666;
}

a:-webkit-any-link {
  text-decoration: none;
}

a:-moz-any-link {
  text-decoration: none;
}

a:any-link {
  text-decoration: none;
}

.slider {
  width: 100%;
  height: 430px;
  background-color: #e8e8e8;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: Georgia;
}

.slider-rectangle {
  width: 960px;
  height: 70px;
  background-color: #ffffff;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.button-right {
  margin-left: 5px;
  margin-right: 37px;
}

.slider-background {
  width: 100%;
  height: 360px;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
}

.image {
  width: 210px;
  height: 370px;
  display: inline-block;
  line-height: 100px;
  position: relative;
  bottom: 20px;
}

.slider-wrapper {
  width: 900px;
  display: flex;
  justify-content: space-around;
}

.services {
  width: 1000px;
  height: 180px;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  margin-top: 50px;
  font-family: Georgia;
}

.services-container {
  width: 920px;
  height: 180px;
  display: flex;
  justify-content: center;
}

.services-content-box {
  width: 250px;
  height: 150px;
  padding: 5px;
}

.services-title {
  color: #666666;
  font-size: 18px;
  line-height: 21.6px;
  margin-bottom: 18px;
}

.services-box {
  width: 210px;
  height: 100px;
}

.services-box-title {
  color: #e2494b;
  font-size: 18px;
  font-weight: 400;
  line-height: 21.6px;
  margin-bottom: 18px;
}

.services-icon {
  width: 48px;
  height: 48px;
  background-color: #353535;
  float: left;
  margin-right: 14px;
  text-align: center;
  font-family: Georgia;
  font-size: 12px;
  line-height: 14.4px;
  color: #ffffff;
  padding-top: 2px;
}

.services-content {
  color: #999999;
  font-size: 13px;
  line-height: 18.2px;
}

.services-splitter {
  width: 920px;
  height: 2px;
  background-color: #999999;
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(#fafafa),
    color-stop(50%, #e8e8e8),
    to(#fafafa)
  );
  background-image: linear-gradient(
    to right,
    #fafafa 0%,
    #e8e8e8 50%,
    #fafafa 100%
  );
  vertical-align: middle;
  margin-left: 20px;
}

.latestWork {
  width: 1000px;
  height: 274px;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  margin-top: 50px;
  font-family: Georgia;
}

.latestWork-container {
  width: 920px;
  height: 180px;
  display: flex;
}

.latestWork-content-box {
  width: 250px;
  height: 274px;
  padding: 5px;
}

.latestWork-title {
  color: #666666;
  font-size: 18px;
  margin-bottom: 18px;
}

.latestWork-summary {
  color: #999999;

  font-size: 13px;
  line-height: 18.2px;
}

.latestWork-box {
  width: 210px;
  height: 100px;
}

.latestWork-img {
  width: 210px;
  height: 140px;
  background-color: #333333;
  color: #f8f8f8;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  padding-top: 64px;
}

.latestWork-describe {
  color: #999999;
  font-size: 13px;
  margin-top: 23px;
  line-height: 18.2px;
}

.latestWork-link {
  color: #e2494b;
  font-size: 13px;
}

.footer {
  width: 1000px;
  height: 480px;
  background-color: #e8e8e8;
  font-family: Georgia;
}

.footer-rectangle-container {
  width: 1000px;
  height: 70px;
  display: flex;
  justify-content: center;
}

.footer-rectangle {
  width: 960px;
  height: 70px;
  background-color: #ffffff;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 22px;
  padding-right: 30px;
}

.footer-social-container {
  display: flex;
  justify-content: center;
}

.social-circle {
  border-radius: 50%;
  width: 32px;
  height: 32px;
  background-color: #333333;
  margin: 8px;
}
/* .button-up {
} */

.footer-box {
  width: 1000px;
  height: 320px;
  padding: 20px;
  display: flex;
}
.footer-content-box {
  width: 250px;
  height: 340px;
  padding: 5px;
  background-color: #e8e8e8;
}
.footer-title {
  color: #979797;
  font-size: 12px;
  font-weight: bold;
  line-height: 14.4px;
  text-transform: uppercase;
  margin-top: 20px;
  margin-bottom: 20px;
}

.footer-img-frame {
  width: 223px;
  height: 98px;
  border: 1px solid #dbdbdb;
  display: flex;
  margin-bottom: 20px;
}

.footer-img-aboutUs {
  width: 215px;
  height: 90px;
  background-color: #333333;
  color: #f8f8f8;
  font-size: 16px;
  text-align: center;
  margin: auto;
  padding-top: 39px;
}
.footer-about-us {
  width: 206px;
  height: 90px;
}
.footer-txt {
  color: #999999;
  font-size: 12px;
  line-height: 19.2px;
}

.footer-link-separator {
  width: 210px;
  height: 1px;
  background-color: #d4d4d4;
  margin: 2px;
}

.arrow {
  font-size: 12px;
  color: #999999;
}

.footer-link {
  color: #e2494b;
  font-size: 12px;
}
.footer-post-cont-title {
  color: #979797;
  font-size: 12px;
  font-weight: bold;
  line-height: 14.4px;
  text-transform: uppercase;
  margin-top: 20px;
  margin-bottom: 0px;
}

.footer-blog-art {
  width: 220px;
  height: 100px;
  margin-bottom: 25px;
}

.footer-post-title {
  font-family: Georgia;
  font-size: 14px;
  font-weight: bold;
  line-height: 16.8px;
  color: #999999;
  margin-top: 25px;
}
.footer-post-date {
  font-size: 10px;
  line-height: 12px;
  color: #999999;
}

.footer-contact {
  height: 31px;
  width: 225px;
  border-radius: 5px;
  background-color: #444444;
  font-family: Georgia;
  color: #ffffff;
  font-size: 13px;
  margin-bottom: 10px;
  padding: 7px;
}

.footer-contact-message {
  height: 110px;
  width: 225px;
  border-radius: 5px;
  background-color: #444444;
  color: #ffffff;
  font-family: Georgia;
  font-size: 13px;
  margin-bottom: 10px;
  padding: 7px;
  vertical-align: top;
}

.footer-splitter {
  width: 960px;
  height: 2px;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(50%, #c3c3c3), color-stop(50%, #ffffff));
  background-image: linear-gradient(#c3c3c3 50%, #ffffff 50%);
  margin-left: 20px;
  margin-top: 20px;
}

.footer-footer {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.copright {
  color: #999999;
  font-size: 12px;
  margin-left: 20px;
}

.autor {
  color: #999999;
  font-size: 12px;
  text-align: right;
  margin-right: 20px;
}

